window.dom = {
  find(selector) {
    return document.querySelectorAll(selector)
  },
  style(node, name, value) {
    if (arguments.length === 3) {
      node.style[name] = value;
    } else if (arguments.length === 2) {
      if (typeof name ==='string') {          //instanceof ËøêÁÆóÁ¨¶Áî®‰∫éÊ£ÄÊµãÊûÑÈÄ†ÂáΩÊï∞ÁöÑ prototype Â±ûÊÄßÊòØÂê¶Âá∫Áé∞Âú®Êüê‰∏™ÂÆû‰æãÂØπË±°ÁöÑÂéüÂûãÈìæ
        console.log(name);
        return node.style[name]
      } else if (typeof name === 'object') {   
        const object = name
        for (let k in object) {
          node.style[k] = object[k]
        }
      }
    }
  },
  each(nodeList,fn) {
    for (let i = 0; i < nodeList.length; i++){
      fn.call(null,nodeList[i])
    }
  },
};

const div = dom.find("#test>.red")[0]; // Ëé∑ÂèñÂØπÂ∫îÁöÑÂÖÉÁ¥†
